import React, {Component} from 'react';
import './index.scss';
import './lottery.css';

const thanks = require('../../assect/hand.png'); // 谢谢参与
const redBag = require('../../assect/minibag.png'); // 红包

const ITEMS = [
    {img:redBag,title:'0.5元',value: 0.5},
    {img:thanks,title:'谢谢参与'},
    {img:redBag,title:'0.88元',value: 0.88},
    {img:redBag,title:'2元',value: 2.00},
    {img:redBag,title:'0.66元',value: 0.66},
    {img:thanks,title:'谢谢参与'},
    {img:redBag,title:'1元',value: 1.00},
    {img:redBag,title:'5元',value: 5.00}
]

let timer;
let count = 8; // 总共有多少个位置
let speed = 200; // 初始转动速度
let times = 0; // 转动次数
const cycle = 50; // 转动基本次数：即至少需要转动多少次再进入抽奖环节

export default class Lottery extends Component {
    constructor(props) {
        super(props);
        this.state = {
            mode: 'tips',
            done: false,
            showThanks: false,
            showRedBag: false,
            price: '',
            index: -1,
            prize: -1,
        }
    }

    startLottery = () => {
        const {done} = this.state;
        if(done){return}
        this.setState({
            done: true
        }, () => {
            this.startRoll();
        })		
    }

    startRoll = () => {
        const {prize, index} = this.state;
        times += 1; // 转动次数+1
        this.oneRoll();
        if (times > cycle + 10 && prize === index) {
            // 展示抽奖结果
            clearTimeout(timer);
            times = 0;
            speed = 200;
            this.setState({
                prize: -1,
                index
            }, () => {
                const result = ITEMS[index];
                if(result.value) {
                    // 红包，展示金额
                    this.setState({
                        showRedBag: true,
                        price: result.value
                    })
                }else {
                    // 未中奖
                    this.setState({
                        showThanks: true
                    });
                }
            });
        } else {
            if (times < cycle) {
                speed -= 10;
            } else if (times === cycle) {
                // 转够50次后，随机设置中将目标
                this.setState({
                    prize: Math.ceil(Math.random() * 7)
                    // prize: Math.ceil(Math.random() * 2) > 1 ? 1 : 5 // 在两个谢谢间随机
                });
            } else if (times > cycle + 10 && ((prize === 0 && index === 7) || prize === index + 1)) {
                speed += 100;
            } else {
                speed += 20;
            }
            if (speed < 40) {
                speed = 40
            }
            timer = setTimeout(this.startRoll, speed)
        }
    }

    oneRoll = () => {
        const {index} = this.state;
        this.setState({
            index: (index > count - 1) ? 0 : (index + 1)
        })
    }

    render() {
        const {mode, index, showThanks, showRedBag, price} = this.state;
        if(mode === 'tips') {
            return (
                <div style={{paddingTop: '50%', textAlign: 'center'}}>
                    <img style={{width: '50%'}} src={require('../../assect/done.png')} alt='快去抽奖吧' />
                    <div style={{margin: '12px 0', fontSize: '17px'}}>感谢参与</div>
                    <div
                        style={{
                            width: 160,
                            margin: '20px auto',
                            backgroundColor: '#4AB0FF',
                            height: 40,
                            lineHeight: '40px',
                            borderRadius: 20,
                            color: '#fff',
                            fontSize: 20
                        }}
                        onClick={() => {this.setState({mode: 'lottery'})}}
                    >
                        去抽奖
                    </div>
                </div>
            )
        }else {
            return (
                <div className='lotteryWrapper'>
                    <div style={{paddingTop: '20%', color: '#fff', textAlign: 'center', fontSize: 20, fontWeight: 'bold', lineHeight: 2}}>
                        <p>感谢您参与本次的问卷调查</p>
                        <p>赶快去参加抽奖吧</p>
                    </div>
                    <div className="lottery">
                        <div className="lottery-item">
                            <div className="lottery-start">
                                <div className="box" onClick={this.startLottery}>
                                    <p><b>抽奖</b></p>
                                </div>
                            </div>
                            <ul>
                                {
                                    ITEMS.map((item, i) => (
                                        <li key={i} className={index === i ? 'on' : ''}>
                                            <div className="box">
                                                <p><img src={item.img} alt={item.title} /></p>
                                                <p>{item.title}</p>
                                            </div>
                                        </li>
                                    ))
                                }
                            </ul>
                        </div>
                    </div>
                    {
                        showThanks ? <div className='thank_join'>谢谢参与</div> : null
                    }
                    {
                        showRedBag ? (
                            <div className='marsk'>
                                <div className='content'>
                                    <div className='price'>
                                        <p className='tips'>恭喜抽中现金红包</p>    
                                        <p className='moneyWrapper'>
                                            <span className='icon'>￥</span>
                                            <span className='money'>{price.toFixed(2)}</span>
                                        </p>
                                        <p className='desc'>
                                            <span>红包奖励将会在1个工作日内汇至您的微信钱包，</span><br />
                                            <span>请注意查收</span>
                                        </p>
                                    </div>                                    
                                </div>
                                <div className='btn'>确定</div>
                            </div>
                        )
                        :
                        null
                    }
                </div>
            )
        }        
    }
}