import React, {Component} from 'react';
import { Picker, List, Toast } from 'antd-mobile';
import question from './question';
import './index.scss';

const CHECK = require('../../assect/check.png');
const UNCHECK = require('../../assect/uncheck.png');

export default class Answer extends Component {
    constructor(props) {
        super(props)
        this.state = {
            current: question[1], // 当前所在答题页
            currentPage: 1, // 当前题目序号
            nextPage: 2,
            answers: {}, // 所选答案
            // 所有区域信息 后台接口获取
            areaInfos: [
                {
                    label: '新华区',
                    value: '新华区'
                },
                {
                    label: '运河区',
                    value: '运河区'
                },
                {
                    label: '沧县',
                    value: '沧县'
                },
                {
                    label: '东光县',
                    value: '东光县'
                },
                {
                    label: '盐山县',
                    value: '盐山县'
                },
                {
                    label: '肃宁县',
                    value: '肃宁县'
                },
                {
                    label: '吴桥县',
                    value: '吴桥县'
                },
                {
                    label: '孟村回族自治县',
                    value: '孟村回族自治县'
                },
                {
                    label: '泊头市',
                    value: '泊头市'
                },
                {
                    label: '河间市',
                    value: '河间市'
                }
            ]
        }
    }

    // 检测当页是否已填写
    checkAnswer = () => {
        const {current, answers} = this.state;
        const {key} = current;
        return (answers[key] && answers[key].length);
    }

    next = () => {
        const done = this.checkAnswer();
        if(done) {
            // 检测通过
            const {nextPage} = this.state;
            if(nextPage === 'fail' || nextPage === 'success') {
                const {end} = this.props;
                end && end(nextPage);
            }else {
                // 继续回答下一题
                this.setState({
                    current: question[nextPage],
                    currentPage: nextPage
                });
            }            
        }else {
            // 未填写
            Toast.info('请选择！', 1)
        }
    }

    // 勾选/取消勾选
    toggleSelect = item => {
        const {current, answers} = this.state;
        if(current.type === 'radio') {
            // 单选
            answers[current.key] = [item.value];
            this.setState({
                answers,
                nextPage: item.jumpTo
            })
        }else {
            // 多选
            const selected = answers[current.key]||[];
            if(selected.includes(item.value)) {
                answers[current.key] = selected.filter(value => value !== item.value);
                // 取消勾选
            }else {
                // 确认勾选
                selected.push(item.value);
                answers[current.key] = selected;
            }
            this.setState({
                answers,
                nextPage: current.jumpTo
            });
        }
    }

    // 选择区域
    selectedArea = value => {
        const {current, answers} = this.state;
        answers[current.key] = value;
        this.setState({
            answers
        });
    }

    render() {
        const {current, currentPage, answers, areaInfos} = this.state;
        const selected = answers[current.key]||[];
        return (
            <div className='answerWrapper'>
                <div className='container'>
                    <div className='title'>{`${currentPage}、${current.title}`}</div>
                    <div className='options'>
                        {
                            current.type === 'select' ? (
                                <Picker
                                    onOk={this.selectedArea}
                                    cols={1}
                                    data={areaInfos}
                                    value={selected}
                                >
                                    <List.Item arrow="horizontal">
                                        <span style={{fontSize: 16, color: '#362208'}}>所在地</span>
                                    </List.Item>
                                </Picker>
                            )
                            :
                            (
                                current.options.map(item => (
                                    <div key={item.value} className='item' onClick={() => {this.toggleSelect(item)}}>
                                        <img
                                            src={selected.includes(item.value) ? CHECK : UNCHECK}
                                            style={{marginRight: 10}} 
                                            width={16} 
                                            height={16}  
                                            alt='check' 
                                        />
                                        <span className='label'>{item.label}</span>
                                    </div>
                                ))
                            )
                        }
                    </div>
                </div>
                <div className='action'>
                    <span onClick={this.next} className='btn'>下一题&nbsp;&gt;</span>
                </div>
            </div>
        )
    }
}