import React, {Component} from 'react';
// import axios from 'axios';
// 首页
import HomePage from './components/homePage';
// 答题页
import Answer from './components/answer';
// 抽奖页
import Lottery from './components/lottery';
// 不符合要求页
import NonConformance from './components/nonConformance';

export default class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      accord: true, // 是否符合要求
      currentPage: 'home'
    }
  }

  componentDidMount() {
    const {search} = window.location;
    const userId = ((search||'').split('=')||[])[1];
    // 根据获取到的userId请求接口判断是否符合要求
    if(userId) {
      // axios.get('')
    }
  }

  // 开始答题
  start = () => {
    console.log('开始答题')
    const {accord} = this.state;
    this.setState({
      currentPage: accord ? 'answer' : 'out'
    })
  }

  // 答题结束
  end = type => {
    // 根据答题结果，进入抽奖页或不符合要求页
    this.setState({
      currentPage: type === 'fail' ? 'out' : 'lottery'
    });
  }

  render() {
    const {currentPage} = this.state;
    return (
      <div style={{height: '100vh', backgroundColor: '#fff', overflow: 'hidden'}}>
        {
          currentPage === 'home' && <HomePage start={this.start} />
        }
        {
          currentPage === 'answer' && <Answer end={this.end} />
        }
        {
          currentPage === 'lottery' && <Lottery />
        }
        {
          currentPage === 'out' && <NonConformance />
        }
      </div>
    );
  }
}
