import React, {Component} from 'react';
import './index.scss';

export default class HomePage extends Component {
    render() {
        const {start} = this.props;
        return (
            <div className ='wrapper'>
            <div className='container'>
                <div className='descWrapper'>
                    {/* 人员图片 */}
                    <img className='people' src={require('../../assect/people.png')} alt='people' />
                    {/* 有奖说明文字背景图 */}
                    <img style={{width: '90%'}} src={require('../../assect/desc.png')} alt='desc' />
                    {/* 说明内容 */}
                    <div className='descContainer'>                    
                        <div className='descTitle'>
                        <img style={{height: '12px'}} src={require('../../assect/left.png')} alt = 'left' />
                        <div style={{margin: '0 8px', color: '#EB6459', fontSize: '16px'}}>有奖说明</div>
                        <img style={{height: '12px'}} src={require('../../assect/right.png')} alt='right' />
                        </div>
                        <p className='text'>为深入了解人民群众对公安机关的新期待、新要求，以便有针对性地改进公安工作，维护社会稳定，我们特组织开展市民对辖区安全感和满意度问卷调查，每月作答成功的用户，将会获得限定名额的红包奖励。</p>
                    </div>
                </div>
                <div className='btn' onClick={() => {start&&start()}}>
                    开始答题
                </div>
            </div>
            </div>
        );
    }
}
